.text-input {
    width: 100%;
    padding: 8px 0;

    &__label {
        display: block;
        margin-bottom: 10px;
    }
    &__input {
        width: 100%;
        background: $text-input-bg;
        border-radius: 99px;
        border: none;
        outline: none;
        padding: 10px 15px;
        color: white;
        font-size: 1.125em;
    }
}