@keyframes fadein {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

.navbar {
	min-height: 100%;
	width: $navbar-width;
	background: $solid-background;
	box-shadow: 0 0 4px 5px rgba($color: black, $alpha: 0.25);
	padding: 30px 0px;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;

	z-index: 1;
}

.selected-box {
	height: 100%;
	width: 100%;
	background: $primary-selected;
	border-radius: 20px;
	animation: fadein 1s ease;

	z-index: 2;
}

.navbar__icon__group {
	height: 70px;
	width: 70px;
	margin-bottom: 20px;

	position: relative;

	&:hover {
		cursor: pointer;
	}
}

.navbar__icon {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);

	z-index: 3;
}

.navbar__account {
	background: $primary-selected;
	width: 60px;
	border-radius: 999px;
	opacity: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	transition: all 0.5s ease;

	svg {
		transition: all 0.5s ease;
	}

	&--hover {
		@extend .navbar__account;

		padding: 30px 0 60px 0;
		opacity: 1;

		svg {
			margin-bottom: 25px;
		}
	}
}

.navbar__useravatar {
	height: 60px;
	width: 60px;
	border-radius: 50%;
	position: absolute;
	bottom: 0;
	transform: translateY(-50%);

	&:hover {
		cursor: pointer;
	}
}
